body {
  font-family: Arial, sans-serif;
  background: linear-gradient(to right, #00c6ff, #0072ff);
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: white;
}

.login-container {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  max-width: 400px;
  width: 90%;
  color: #333;
}

h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.button-row,
.line-button-row {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  margin-bottom: 1rem;
}

.line-button-row {
  justify-content: center;
}

button {
  background: #00c6ff;
  border: none;
  padding: 1rem;
  color: white;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
  width: 48%;
}

.line-login-button {
  width: 100%; /* Line 登入按鈕獨佔整行 */
}
.email-register-box {
  /* 其他原有的樣式 */
  margin-bottom: 20px; /* 增加 email-register-box 容器與下方元素的距離 */
}

.register-button {
  /* 其他原有的樣式 */
  margin-bottom: 10px; /* 增加按鈕與返回鍵的距離 */
}

.back-button {
  /* 其他原有的樣式 */
  margin-top: 10px; /* 增加返回鍵與按鈕的距離 */
}

button:hover {
  background: #0072ff;
}


.profile-box img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 1rem;
}

.profile-box h2 {
  margin: 0.5rem 0;
}

.form-group {
  margin-bottom: 1rem;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

input {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
}

input:focus {
  outline: none;
  border-color: #00c6ff;
}

/* 響應式設計 */
@media (max-width: 768px) {
  .button-row,
  .line-button-row {
    flex-direction: column;
    gap: 0.5rem;
  }

  button {
    width: 100%;
  }
}
